@import (less) "./../less/color.less";

.switch-container {
    background-color: #258900;
    border-radius: 0px;
    box-shadow: inset 0em 0.125em 0.188em 0 rgba(0,0,0,.2);
}

.focusTabSwitchOn {
    box-shadow: 0 0 0.05em 0.23em rgba(37,137,0,0.6) !important;
}

.switch-button-icontext {
    position: relative;
    font-weight: bold;
    font-size: 0.750em;
    margin: 0 !important;
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1;
    outline: none !important;
    min-width: 6.875em;
    height: 4.167em;
    z-index: 0;
    &:active {
        box-shadow:  inset 0em 0.125em 0.188em 0 rgba(0,0,0,.2);
    }
    // &:focus {
    //     box-shadow: 0 0 0.05em 0.23em rgba(37,137,0,0.6) !important;
    // }

    .switch-label {
        margin-right: 0.625em;
    }
    .switch-label-icon {
        padding-bottom: 0.313em;
        display: table-cell !important;
        vertical-align: middle;
    }
    .switch-icon {
        font-size: 2.084em !important;
        display: table-cell !important;
    }
    .switch-icon-text-up {
        display: block !important;
    }
    &.btn {
        border-radius: 0 !important;
    }
    .btn[disabled] {
        &:hover {
            cursor: default;
        }
    }
}
.primary {
    border: none;
}
.primary.right {
    box-shadow:  inset 0em 0.125em 0.188em 0 rgba(0,0,0,.2) !important;
}
.switch-button-icontext.primary {
    color: @white;
    background-color: @green;
    border-color: @green;
    box-shadow:  inset 0em 0.125em 0.188em 0 rgba(0,0,0,.2);
}
.switch-button-icontext.default {
    color: @gray;
    background-color: @white;
    border-color: @light_gray;
}
.switch-button[disabled] {
    box-shadow: none;
}
.switch-button-icontext.default[disabled] {
    &:hover {
        background-color: @white !important;
        border-color: @light_gray !important;
    }
}
.switch-button-icontext.primary[disabled] {
    &:hover {
        background-color: @green !important;
        border-color: @green !important;
    }
}


