// binary
.switch-binary {
    position: relative;
    display: inline-block;
    width: 6.250em;
    height: 3.125em;
    cursor: pointer;
    background-color: @light_gray;
    -webkit-transition: 0s;
    transition: 0s;

    .switch-label-icon-on {
        font-weight: bold;
        // font-size: 0.750em;
        color: @white;
        width: 3.125em;
        display: table-cell;
        line-height: 3em;
        box-shadow: inset 0em 0.125em 0.188em 0 rgba(0,0,0, 0.2);

    }

    .switch-label-icon-off {
        font-weight: bold;
        // font-size: 0.750em;
        color: @gray;
        width: 3.125em;
        display: table-cell;
        line-height: 3em;
        box-shadow: inset 0em 0.125em 0.188em 0 rgba(0,0,0, 0.2);
    }

    .binary-label {
        font-size: 0.75em;
    }
}


.slider-box {
  position: absolute;
  content: "";
  cursor: pointer;
  height: 3.125em;
  width: 3.125em;
  left: 0;
  bottom: 0;
  background-color: white;
  -webkit-transition: 0s;
  transition: 0s;
  border: 0.063em solid @light_gray
}

.switch-binary-true {
    background-color: @green;
}

.switch-binary-true .slider-box {
  -webkit-transform: translateX(3.125em);
  -ms-transform: translateX(3.125em);
  transform: translateX(3.125em);
}


:host-context(.contrasto-on) {

    .switch-button-icontext.default,
    .switch-button-icontext-small.default,
    .switch-button-text.default,
    .switch-button-icon.default {
        color: @black;
        background-color: @white;
        border-color: @black;
    } 

    .switch-button-icontext.primary,
    .switch-button-icontext-small.primary,
    .switch-button-text.primary,
    .switch-button-icon.primary,
    .switch-binary {
        color: @white;
        background-color: @black;
        border-color: @white;
    }

    .slider-box {
        background-color: @white;
        border-color: @black;
    }

    .switch-binary {
        .switch-label-icon-on {
            color: @white;
        }
        .switch-label-icon-off {
            color: @white;
        }
    }

    .switch-binary-true {
        background-color: @black;
    }
}